import React from "react";
import Container from "react-bootstrap/Container";

import "./EasterEgg.css";

import Baguette from "./baguette.png";
import Wine from "./wine.png";
import Croissant from "./croissant.png";

const EasterEgg = (props) => {
  const countDownDate = new Date("June 28, 2021 02:50:00").getTime();
  const beginningDate = new Date("January 7, 2021 10:55:00").getTime();

  // Update the count down every 1 second
  const x = setInterval(function () {
    // Get today's date and time
    const now = new Date().getTime();

    // Find the distance between now and the count down date
    const distance = countDownDate - now;
    const totalDistance = countDownDate - beginningDate;

    // Time calculations for days, hours, minutes and seconds
    const days = Math.floor(distance / (1000 * 60 * 60 * 24));
    const hours = Math.floor(
      (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
    );
    const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((distance % (1000 * 60)) / 1000);

    const totalSeconds = Math.floor(
      days * 86400 + hours * 3600 + minutes * 60 + seconds
    );

    const daysToDate = Math.floor(totalSeconds / 86400);
    const daysFromBeginning = Math.floor((now - beginningDate) / 86400);
    const diff = countDownDate - beginningDate;
    const totalDays = diff / (1000 * 60 * 60 * 24);

    const percent = ((1 - totalSeconds / 86400 / totalDays) * 100).toFixed(5);

    // Display the result in the element with id="demo"
    document.getElementById("countdownDiv").innerHTML =
      days + "d " + hours + "h " + minutes + "m " + seconds + "s ";
    document.getElementById("secondsDiv").innerHTML = totalSeconds + " seconds";
    document.getElementById("percentDiv").innerHTML =
      percent + "% completed since 7th Jan 2021";
  }, 1000);
  return (
    <Container fluid={true} className="container-fluid easter-egg">
      <div className="text">
        <h1>ON RENTRE DANS</h1>
        <div className="all-texts">
          <div id="countdownDiv" />
          <div id="secondsDiv" />
          <div id="percentDiv" />
        </div>
      </div>

      <div className="snowflake">
        <img src={Baguette} />
      </div>
      <div className="snowflake">
        <img src={Wine} />
      </div>
      <div className="snowflake">
        <img src={Croissant} />
      </div>
      <div className="snowflake">
        <img src={Baguette} />
      </div>
      <div className="snowflake">
        <img src={Wine} />
      </div>
      <div className="snowflake">
        <img src={Croissant} />
      </div>
      <div className="snowflake">
        <img src={Wine} />
      </div>
      <div className="snowflake">
        <img src={Baguette} />
      </div>
      <div className="snowflake">
        <img src={Wine} />
      </div>
      <div className="snowflake">
        <img src={Baguette} />
      </div>
      <div className="snowflake">
        <img src={Wine} />
      </div>
      <div className="snowflake">
        <img src={Baguette} />
      </div>
      <div className="snowflake">
        <img src={Croissant} />
      </div>
      <div className="snowflake">
        <img src={Wine} />
      </div>
      <div className="snowflake">
        <img src={Croissant} />
      </div>
      <div className="snowflake">
        <img src={Wine} />
      </div>
      <div className="snowflake">
        <img src={Baguette} />
      </div>
      <div className="snowflake">
        <img src={Wine} />
      </div>
    </Container>
  );
};

export default EasterEgg;
