import React from "react";
import "./App.css";

import "bootstrap/dist/css/bootstrap.min.css";

import Navigation from "./components/User/Navigation/Navigation";
import ExperienceContainer from "./components/User/ExperienceContainer/ExperienceContainer";
import SkillsContainer from "./components/User/SkillsContainer/SkillsContainer";
import About from "./components/User/About/About.component";
import Contact from "./components/User/Contact/Contact.component";
import EasterEgg from "./components/User/EasterEgg/EasterEgg";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      urlWebsite: "https://monkeyfeeder.github.io/mael-landrin",
      easterEggActivated: false,
      // isLoggedIn: false,
    };
  }

  componentDidMount() {
    // this.setState({isLoggedIn: true});
  }

  activateEasterEgg = () => {
    this.setState({ ...this.state, easterEggActivated: true });
  };

  render() {
    return (
      <div className="App">
        {this.state.easterEggActivated ? (
          <EasterEgg />
        ) : (
          <>
            <Navigation />
            <About />
            <ExperienceContainer />
            <SkillsContainer
              urlWebsite={this.state.urlWebsite}
              activateEasterEgg={this.activateEasterEgg}
            />
            <Contact />
          </>
        )}
      </div>
    );
  }
}

export default App;
